html{
  overflow: scroll;
}

main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width:100%;
}

.container{
  display: grid;
  box-sizing: border-box;
}

.title {
  margin: 1em;
  text-align: center;
  font-family: var(--sans-font);
  font-size: 1em;
  font-weight: 400;
  color: white;
  user-select: none;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}





.value-box{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: .4em;
  width: 280px;
  height: 77px;
  background: var(--main-wht-color);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.value-box-content{
  color: var(--main-bg-color);
  font-family: var(--sans-font);
  font-size: 1.5em;
  font-weight: 600;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  user-select: none;
}

.value-box-icons{
  display: flex;
  flex-direction: row;
  gap: 10px;
  color: var(--main-bg-color);
  /* align-items: center;
  justify-content: center; */
  align-content: center;
}


.icon-div-value-box{
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
  width: 26px;
  height: 26px;
  border-radius: 4px;
  background-color: var(--main-wht-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.icon-div-cb {
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
  width: 20px;
  height: 20px;
  border-radius: 2px;
  color: black;
  background-color: var(--main-wht-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}




.add-value-text{
  font-family: var(--sans-font);
  font-size: 1em;
  margin: 1em;
  color: var(--main-wht-color);
  opacity: .8;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  user-select: none;
  text-align: center;
}


.child-boxes{
  display: flex;
  flex-direction: column;
  justify-self: right;
}

.cb{
  margin-top: .2em;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 260px;
  min-height: 60px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.cb.bg-white {
  background: var(--main-wht-color);
}


.cb.bg-blue{
  background: var(--main-bg-color);
  border: 1px solid var(--main-wht-color);
  font-family: var(--sans-font);
  font-size: .9em;
  font-weight: 500;
  color: var(--main-wht-color);
  cursor: pointer;
}



.content{
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  user-select: none;
}

.do-text{
  color: rgb(46, 46, 46);
  font-family: var(--sans-font);
  font-size: .8em;
  font-weight: 500;
  margin: 1em 0em;
  margin-right: 10px;
  line-height: 95%;
}

.value-description{
  color: var(--main-bg-color);
  font-family: var(--sans-font);
  font-size: .8em;
  font-weight: 500;
  margin: 1em 0em;
  margin-right: 10px;
  
}

.cb-icons{
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.bg-blue-input{
  border: 1px solid var(--main-wht-color);
  background-color: var(--main-bg-color);
  height: 2em;
  color: var(--main-wht-color);
  font-family: var(--sans-font);
  font-size: .9em;
  font-weight: 500;
}

.bg-blue-input:focus{
  outline: 1.75px solid var(--main-wht-color)
}

.selectValue-page{
  margin-top: .4em;
  background-color: var(--main-bg-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.selectValue-container{
width: 280px;
}

.footer{
  position: absolute;
  top: 90vh;
  color: var(--main-wht-color);
  font-family: var(--sans-font);
  font-size: .8em;
  opacity: .8;
}

.footer>a{
  color: var(--main-wht-color);
}